@import "reset";
@import url('https://fonts.googleapis.com/css2?family=Lora&family=Merriweather:ital,wght@0,400;0,700;1,400&display=swap');

// Typography

html {font-size: 100%;} /*16px*/

body {
  // background: white;
  font-family: 'Merriweather', serif;
  font-weight: 400;
  line-height: 1.45;
  // color: #000000;
}

p {margin-bottom: 1rem;}

h1, h2, h3, h4, h5 {
  margin: 3rem 0 1.38rem;
  font-family: 'Lora', serif;
  font-weight: 400;
  line-height: 1.3;
}

h1 {
  margin-top: 0;
  font-size: 3.052rem;
}

h2 {font-size: 2.441rem;}

h3 {font-size: 1.953rem;}

h4 {font-size: 1.563rem;}

h5 {font-size: 1.25rem;}

small, .text_small {font-size: 0.8rem;}

a, a:visited {
  color: maroon;
  text-decoration: none;
  transition: .3s;

  &:hover {
    color: #b04f4d;
  }
}

// Layout styles

.container {
  margin: 0 1.25rem;
}

header {
  padding: .5rem 0;

  .container {
    display: grid;
    grid-template-areas: "avatar info" "nav nav";
    margin: 0 .625rem;
  }

  .site-name {
    margin-bottom: .25rem;
    font-size: 1.563rem;    
  }

  .site-description {
    font-size: 0.8rem;
    margin-bottom: 0;
  }

  .site-avatar {
    text-align: right;
    margin-right: .25rem;

    img {
      border-radius: 50%;
      width: 3.5rem;
    }
  }

  .site-info {
    padding-left: .5rem;
    align-self: center;
  }

  nav {
    grid-area: nav;
    display: flex;
    justify-content: space-evenly;
    border-top: 1px solid lightgray;
    padding: .5rem 0 0;
    margin-top: .5rem;
  }
  
}

section.home {
  background: url(/images/headshot.jpg) center center / cover;
  width: 100vw;
  height: 100vh;
}

video { width: 100% }

@media screen and (min-width: 600px) {
  .page.container {
    margin: 0 auto;
    max-width: 55ch;
  }
}